.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sidebar {
  margin-top:16px;
}

.card-header {
  text-align: left;
}

.card {
  margin-bottom: 12px;
}

.bar {
  fill: #385b96;
  transition:0.2s;
}
.bar:hover {
  fill: #538cea;
}

.chart-tooltip {
  position: absolute;
  white-space: nowrap;
  background: #ffffffdb;
  font-size: 12px;
  padding: 2px 6px;
  color: #404040;
  border-radius: 4px;
  border: 1px solid #bbbbbb;
}

.pie-slice {
  stroke-width: 1px;
  opacity: 0.7;
}

.pie-slice:hover {
  opacity: 1;
}

.question-group {
  padding: 0 8px;
  margin-bottom: 32px; 
}
