.preferencePage {
  &-container {
    width: 80%;
    margin: 2% auto;
  }

  &-header {
    margin-bottom: 30px;
  }

  &-actions {
    display: flex;
    justify-content: flex-end;
    padding: 2em 0;

    & > * {
      margin: 0 5px;
    }

    button {
      outline: none;
      border-radius: 4px;
      padding: 8px 20px;
      background: #3e51b5;
      border: none;
      color: #fefefe;
      transition: background 500ms, color 500ms, border 500ms;
      -webkit-transition: background 500ms, color 500ms, border 500ms;

      &:hover {
        background: transparent;
        border: solid 1px #3e51b5;
        color: #3e51b5; 
      }
    }
  }

  .diversity-header {
    margin: 40px 0;

    &-title {
      font-size: 1.5em;
      margin-bottom: 10px;
    }
  }
}